* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}
body {
  background-color: #171717;
}
header {
  position: fixed;
  top: 0;
  padding: 15px 80px;
  width: 100%;
  border-bottom: 1px solid rgba(114, 114, 126, 0.2);
  background-color: #171717;
}
header nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo { 
  margin-left: 55px;
  display: inline-block;
  align-items: center;   
  font-size: 30px; 
  color: #877360;
}
.nav-list{
  list-style: none;
}
.item-nav{
  display: inline-block;
  margin-right: 25px;}

.nav-link{
  text-decoration: none;
  color: #877360;
  font-size: 20px;
  padding-bottom: 10px;
  
}
.nav-link:hover{
  border-bottom: 3px solid #FA7D09;
  color: #FAFAFA;
}
main{
  margin-top: 100px;
}
.s-home {
  display: flex;
  padding: 120px 50px;
  justify-content: space-evenly;
  align-items: center;
}

.article-home {
  width: 60%;
  margin-top: 50px;
  margin-left: 2%;
}
.span-home {
  color: #FA7D09;
  font-size: 30px;
}
.article-home h1{
  font-size: 4rem;
  font-weight: 700;
  color: #FAFAFA;
}
.span-home p {
  color: #FAFAFA;
}
.article-home p {
  font-size: 35px;
  color: #FAFAFA;
  margin-top: 10px;
}
.botoes-home{
  margin-top: 30px;
  display: flex;
  align-items: center;
  gap: 20px;
}
.link-contato {
  text-decoration: none;
  color: white;
  background-color: #171717;
  height: 50px;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 500;
  border-radius: 6px;
  border: 1px solid white;

}
.link-contato:hover{
  color: black;
  background-color:#FA7D09;
  transition: 0.5s;
}
.sobre{
  display: flex;
  width: 100%;
  justify-content: center;
  padding: 20px 20px;
  background-color: #191919;
  gap: 60px
}

.titulo-sobre{
  color:#FA7D09;
  text-align: center;
  font-size: 50px;
  display: block;
  padding-top: 70px;  
}
.article-sobre {
  max-width: 600px;
  padding: 70px 20px;
  align-items: flex-end; 
}
.article-sobre p {
  color: #b6aeae;
  font-size: 25px;
  line-height: 30px;
  width: 700px;
}
.titulos{
  text-align: center;
  color: #FA7D09;
  font-size: 50px;
  display: block;
  padding-top: 30px;
  margin-bottom: 50px;
  padding-top: 70px; 
} 
 
.cards{ 
    background-color: #171717;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 60px
}

.projetos-cards {
  background-color: transparent;
  perspective: 1000px;  
  gap: 60px;  
  width: 350px;
  height: 380px; 
}  

.all-card {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d; 
} 

.projetos-cards:hover .all-card {
  transform: rotateY(180deg);
}

.back-card, .front-card {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; 
  backface-visibility: hidden;
} 
.front-card{   
  background-color: #e6c48d;  
  border-radius: 8px;
  padding: 10px;
} 
 
.front-card img { 
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 6px;
  box-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
}  

.back-card{
  background-color: #e6c48d; 
  transform: rotateY(180deg); 
  border-radius: 8px;

} 
.projects-btn{
  padding: 10px 0px;
  margin-top: 10px;
  outline: none;
  font-size: 1.3rem;
  font-weight: 500;
  border-radius: 8px;
  text-decoration: none;
} 
.titulo-card{
  color :#f1f1f1;
  font-size: 26px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-weight: bold;     
}
.texto-card { 
  color :#f1f1f1;
  font-size: 16px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-weight: bold;    
}
.btn-view{ 
  background-color:#FA7D09;
  text-decoration: none;
  outline: none;
  margin: 10px 10px 0px;
  padding: 10px 20px;
  border-radius: 8px;
} 

.btn-git{
  background-color:#FA7D09;
  text-decoration: none;
  outline: none;
  margin: 10px 0px 0px;
  padding: 10px 20px;
  border-radius: 8px  
}
.btn-view:hover{
  background-color: #FAFAFA;
  color: #171717;
  transition: 0.5s;
}
.btn-git:hover{
  background-color: #FAFAFA;
  color: #171717;
  transition: 0.5s;
}
.tecnologia{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: black;
}
.tecnologia-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color:#e6c48d;
  border-radius: 8px;
  padding: 20px 15px;
  box-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
}
.tecnologia-card-title{
  color: white;
  font-size: 30px;
  font-weight: 700;
  margin-top: 20px; 
  padding-bottom: 15px;
} 
.article-contato{
  background-color: #171717;
  justify-content: center; 
  flex-direction: column;
}
.box-contato{
  display: flex; 
  justify-content: center; 

}
.contacts-link{
  display: flex;
  gap: 30px;
}
.contato-title{
  font-size: 50px;
  color: #FAFAFA;
  margin: 70px 50px 30px 50px;

}
form{
  height: 390px;
  width: 390px; 
  padding-top: 30px;
}
form input{
  color: #FAFAFA;
  display: block;
  width: 100%;
  height: 46px;
  border: none;
  border-radius: 6px;
  background-color: #343434;
  margin-bottom: 20px;
  padding-left: 10px;
  outline: none;
}

form textarea {
  background-color: #343434;
  width: 100%;
  height: 100px;
  border: none;
  outline: none;
  border-radius: 6px;
  padding: 10px 0px 0px 10px;
  resize: none;
  color: #FAFAFA;
  margin-bottom: 30px;
}

.button{
  width: 215px;
  height: 45px;
  border-radius: 6px;
  border: none;
  outline: none;
  background-color: #FA7D09;
  font-size: 20px;
  cursor: pointer;
}

footer{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2e2d2d;
  padding: 20px;
  color: #FA7D09;
  font-size: 20px;
  font-weight: 500;
}
